import React from "react";
import ProjectCard from "../../projectCard";
import houseHoldProjectPicture from "../projects/img/thumbnail-household-optimizer.jpg";
import hikingTrailsProjectPicture from "../projects/img/thumbnail-trails-finder.jpg";
import mindsetProjectPicture from "../projects/img/thumbnail-mindset.jpg";
import employeesAppPicture from "../projects/img/thumbnail-employees-directory.jpg";
import budgetAppPicture from "../projects/img/thumbnail-budget-pwa.jpg";
import weatherAppPicture from "../projects/img/thumbnail-weather-checker.jpg";

function Projects(){

    return (
        <div className="container">
            <h3>Projects</h3>
            <div className="row" style={{margin: "3% 3%"}}>
                <div className="col-sm-12 col-md-4">
                <ProjectCard
                        style={{display: "inline-block"}} 
                        name="Mindset for Schools"
                        imgUrl={mindsetProjectPicture}
                        description="This application helps students with their mental health by sharing their moods, and more."
                        repoUrl="https://github.com/jlcalderon/project-3"
                        deployedAppUrl="https://frozen-cliffs-96798.herokuapp.com/"
                    />
                    </div>
                <div className="col-sm-12 col-md-4">
                    <ProjectCard 
                        style={{display: "inline-block"}}
                        name="House Hold Optimizer"
                        imgUrl={houseHoldProjectPicture}
                        description="Application that allows users to keep track of essentials in their household by updating inventory."
                        repoUrl="https://github.com/jlcalderon/project-2"
                        deployedAppUrl="https://vast-mesa-04959.herokuapp.com/"
                    />
                    </div>
                <div className="col-sm-12 col-md-4">
                    <ProjectCard 
                        style={{display: "inline-block"}}
                        name="Hiking Trails Finder"
                        imgUrl={hikingTrailsProjectPicture}
                        description="This app help users find hiking trails and get to know the current weather conditions in those trails."
                        repoUrl="https://github.com/jlcalderon/project-1"
                        deployedAppUrl="https://jlcalderon.github.io/project-1"
                    />
                    </div>
                </div>
                <h3>Sigle apps</h3>
                <div className="row" style={{margin: "3% 3%"}}>
                    <div className="col-sm-12 col-md-4">
                        <ProjectCard 
                            style={{display: "inline-block"}}
                            name="Employees Directory"
                            imgUrl={employeesAppPicture}
                            description="Single app that loads employees information and let user filter by name and sort the results in alphabetical ascending order."
                            repoUrl="https://github.com/jlcalderon/employees-directory"
                            deployedAppUrl="https://jlcalderon.github.io/employees-directory/"
                        />        
                    </div>
                    <div className="col-sm-12 col-md-4">
                        <ProjectCard 
                            style={{display: "inline-block"}}
                            name="Budget PWA"
                            imgUrl={budgetAppPicture}
                            description="This app have functionalities for offline and online, users can add or subtract funds to thier budgets, even when they are offline"
                            repoUrl="https://github.com/jlcalderon/budget-pwa"
                            deployedAppUrl="https://arcane-stream-16974.herokuapp.com/"
                        />        
                    </div>
                    <div className="col-sm-12 col-md-4">
                        <ProjectCard 
                            style={{display: "inline-block"}}
                            name="Weather Checker"
                            imgUrl={weatherAppPicture}
                            description="This app allow users to submit a city location to a list, where they can select one later, and look up for weather conditions."
                            repoUrl="https://github.com/jlcalderon/weatherchecker"
                            deployedAppUrl="https://jlcalderon.github.io/weatherchecker/"
                        />        
                    </div>
                </div>
                </div>
    );
}

export default Projects