import React from "react";
import { Link, useLocation } from "react-router-dom";
import "./index.css";
function Header() {
    //Set state
    const location = useLocation();

    //Handler functions

    //Render
    return (
        <div>
            <div className='row'>
                <div className='col-sm-12 col-md-12'>
                    <header className='main-heading'>
                        <h4>Hey Friends I'm</h4>
                        <h1 className='title'>Jorge Calderon</h1>
                    </header>
                </div>
            </div>
            <div className='row'>
                <div className='col-sm-12 col-md-12'>
                    <nav
                        className='navbar navbar-expand-lg navbar-dark navigation'
                        style={{
                            backgroundColor: "#808080",
                            position: "sticky",
                        }}>
                        <button
                            className='navbar-toggler'
                            type='button'
                            data-toggle='collapse'
                            data-target='#navbarNav'
                            aria-controls='navbarNav'
                            aria-expanded='false'
                            aria-label='Toggle navigation'>
                            <span className='navbar-toggler-icon'></span>
                        </button>
                        <div
                            className='collapse navbar-collapse'
                            id='navbarNav'>
                            <ul className='navbar-nav'>
                                <li className='nav-item active'>
                                    <h4>
                                        <Link to='/portfolio-react'
                                            className=
                                            {location.pathname === "/portfolio-react"
                                                ? "nav-link active"
                                                : "nav-link"}>
                                            About me
                                        </Link>
                                    </h4>
                                </li>
                                <li className='nav-item'>
                                    <h4>
                                        <Link
                                            to='/portfolio'
                                            className={
                                                location.pathname ===
                                                "/portfolio"
                                                    ? "nav-link active"
                                                    : "nav-link"
                                            }>
                                            Portfolio
                                        </Link>
                                    </h4>
                                </li>
                                <li className='nav-item'>
                                    <h4>
                                        <Link
                                            to='/contacts'
                                            className={
                                                location.pathname ===
                                                "/contacts"
                                                    ? "nav-link active"
                                                    : "nav-link"
                                            }>
                                            Contacts
                                        </Link>
                                    </h4>
                                </li>
                                <li className='nav-item'>
                                    <h4>
                                        <a
                                            href='https://jlcalderon.github.io/portfolio/assets/file/JORGE_CALDERON_RESUME.pdf'
                                            className="nav-link">
                                            My Resume
                                        </a>
                                    </h4>
                                </li>
                            </ul>
                        </div>
                    </nav>
                </div>
            </div>
        </div>
    );
}

export default Header;
